.header-section {
	position: relative;
	min-height: 100vh;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
		rect {
			stroke: var(--color-light);
		}
	}
	.elements-absolute {
		@include max-md {
			width: 230%;
		}
	}
	.hogwart-mini {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 32px;
		transform: translate(-50%, -50%);
		z-index: -1;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.image-element {
			position: absolute;
			top: 0;
			right: 0;
			width: 200%;
			height: 100%;
			overflow: hidden;
			.img01,
			.img02 {
				float: right;
				height: 100%;
				width: 50%;
				@include max-lg {
					width: 100%;
				}
			}
			&.foglayer_01 {
				animation: foglayer_01_opacity 10s linear infinite, foglayer_moveme 15s linear infinite !important;
				.img01,
				.img02 {
					background: url("../images/fog.png") center center/cover no-repeat transparent;
				}
			}
			&.foglayer_02 {
				animation: foglayer_02_opacity 21s linear infinite, foglayer_moveme 13s linear infinite !important;
				.img01,
				.img02 {
					background: url("../images/fog2.png") center center/cover no-repeat transparent;
				}
			}
			&.foglayer_03 {
				animation: foglayer_03_opacity 21s linear infinite, foglayer_moveme 13s linear infinite !important;
				.img01,
				.img02 {
					background: url("../images/fog2.png") center center/cover no-repeat transparent;
				}
			}
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		left: 0;
		width: 150%;
		transform: translate(-35%, -40%);
		@include min-lg {
			width: 80%;
			transform: translate(-30%, -50%);
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		transform: translate(0, -40%);
		@include min-lg {
			width: 60%;
			transform: translate(0, -50%);
		}
		.image-element {
			width: 80%;
			@include min-lg {
				width: 60%;
			}
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 50%;
		left: 0;
		width: 100%;
		transform: translate(0, 50%);
		@include min-md {
			bottom: 35%;
		}
		@include min-lg {
			bottom: 30%;
		}
		.image-element {
			width: 150%;
			@include min-lg {
				width: 100%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		top: 15%;
		left: 0;
		width: 100%;
		transform: translate(0, 0);
		@include min-lg {
			top: 0;
			transform: translate(0, 0);
		}
		@include min-lg {
			width: 50%;
		}
	}
	.header-content {
		position: relative;
		height: 100%;
		padding: 6rem 0 0;
		@include max-lg {
			padding: 10rem 0 0;
		}
		@include max-md {
			padding: 8rem 0 0;
		}
		.header {
			position: relative;
			width: 50%;
			height: 100%;
			margin: 0 auto;
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
